.emailform {
    display: flex;
    flex-direction: column;
    padding: 0px 20px 20px 20px;

    label {
        font-size: 18px;
    }

    &__input {
        background: none;
        border-bottom: 1px solid black;
        border-top: none;
        border-left: none;
        border-right: none;
        margin-bottom: 20px;
    }

    &__textarea {
        background: none;
        border-bottom: 1px solid black;
        border-top: none;
        border-left: none;
        border-right: none;
        margin-bottom: 20px;
        height: 100px;
    }

    &__send {
        font-family: 'Special Elite', sans-serif;
        width: fit-content;
        padding: 10px;
        background: black;
        color: white;
        align-self: center;
    }
}

.warning {
    font-size: 20px;
    color: red;
    padding: 20px;
}

.sendright {
    opacity: .2;
    margin: 60px 20px 20px 20px;
    position: absolute;
    right: 0;
    display: inline-block;
}

@media screen and (max-width: 980px) {
    .sendright {
        opacity: .2;
        margin: 20px 20px 20px 20px;
        position: absolute;
        right: 0;
        display: inline-block;
    }
}