.githubfolder {
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    p {
        padding: 0 150px;
    }

    a {
        margin: 20px 0;
        background: black;
        padding: 10px;
        color: white;
        text-decoration: none;
        text-align: center;
    }
}

@media screen and (max-width: 600px) {
    .githubfolder {
        padding-top: 100px;
        margin: 0 20px;
    }
}